import { createAsyncThunk } from '@reduxjs/toolkit';
import { APIRoute } from '../../const';
import { dropToken, saveToken } from '../../services/token';
import { generatePath } from 'react-router-dom';
import { adaptEmployeeActivitiesToClient, adaptEmployeeActivityToClient, adaptEmployeeEducationToClient, adaptEmployeeEducationsToClient, adaptEmployeeLanguages, adaptEmployeeToClient, adaptEmployeesToClient, adaptPersonalDataToClient } from '../../adapters/employees';
export const checkAuthorizationAction = createAsyncThunk('employees/checkAuthorization', async (_arg, { extra: api }) => {
    const { data } = await api.get(APIRoute.Login);
    return adaptEmployeeToClient(data);
});
export const loginAction = createAsyncThunk('employees/login', async ({ loginData, errorHandler }, { extra: api, rejectWithValue }) => {
    try {
        const { data } = await api.post(APIRoute.Login, loginData);
        saveToken(data.token);
        return adaptEmployeeToClient(data);
    }
    catch (err) {
        let error = err;
        if (!error.response) {
            throw err;
        }
        errorHandler(error.response.data);
        return rejectWithValue(error.response.data);
    }
});
export const logoutAction = createAsyncThunk('employees/logout', async (_arg, { extra: api }) => {
    await api.delete(APIRoute.Logout);
    dropToken();
});
export const fetchEmployeePersonalDataAction = createAsyncThunk('employees/fetchEmployeePersonalData', async ({ employeeId }, { extra: api }) => {
    const { data } = await api.get(generatePath(APIRoute.EmployeePersonalData, { employeeId }));
    return adaptPersonalDataToClient(data);
});
export const updateEmployeeAvatarAction = createAsyncThunk('employees/updateEmployeeAvatar', async ({ formData, employeeId, successHandler }, { extra: api }) => {
    formData.append('_method', 'put');
    const { data } = await api.post(generatePath(APIRoute.EmployeeAvatar, { employeeId }), formData);
    successHandler(data);
});
export const deleteEmployeeAvatarAction = createAsyncThunk('employees/deleteEmployeeAvatar', async ({ employeeId, successHandler }, { extra: api }) => {
    await api.delete(generatePath(APIRoute.EmployeeAvatar, { employeeId }));
    successHandler();
});
