const SETTINGS_KEY_NAME = 'hrm-evolet-application-setting';
export const initialSettings = {
    isNavigationCollapsed: false,
};
export const getAppSettings = () => {
    if (localStorage.getItem(SETTINGS_KEY_NAME)) {
        const settings = localStorage.getItem(SETTINGS_KEY_NAME);
        return JSON.parse(settings);
    }
    saveAppSettings(initialSettings);
    return initialSettings;
};
export const saveAppSettings = (settings) => localStorage.setItem(SETTINGS_KEY_NAME, JSON.stringify(settings));
export const dropAppSettings = () => localStorage.setItem(SETTINGS_KEY_NAME, JSON.stringify(initialSettings));
